	module.exports = {"data":{"wordpressPost":{"title":"Healthy foods and diet patterns, patient knowledge and research funding: Most read articles in December","content":"<p>The most-read article in December was Rao <em>et al&#8217;s</em> article on healthier foods and diet patterns and whether they cost more than less healthy options. Carpenter <em>et al</em>&#8216;s article on bed sharing and the risk of SIDS continues to be popular. Other newly-published papers in the top ten most read include Krusche <em>et al</em>&#8216;s evaluation of the feasibility of a web-based mindfulness course for stress and depression.</p>\n<table width=\"483\" border=\"1\" cellspacing=\"0\" cellpadding=\"0\">\n<tbody>\n<tr>\n<td valign=\"top\" width=\"50\">Rank</td>\n<td valign=\"top\" width=\"132\">Author(s)</td>\n<td valign=\"top\" width=\"301\">Title</td>\n</tr>\n<tr>\n<td width=\"50\">1</td>\n<td width=\"132\">Rao <em>et al</em></td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/content/3/12/e004277.full\" target=\"_blank\">Do healthier foods and diet patterns cost more than less healthy options? A systematic review and meta-analysis</a></td>\n</tr>\n<tr>\n<td width=\"50\">2</td>\n<td width=\"132\">Krusche <em>et al</em></td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/content/3/11/e003498.full\" target=\"_blank\">Mindfulness online: an evaluation of the feasibility of a web-based mindfulness course for stress, anxiety and depression</a></td>\n</tr>\n<tr>\n<td width=\"50\">3</td>\n<td width=\"132\">Snow <em>et al</em></td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/content/3/11/e003583.full\" target=\"_blank\">What happens when patients know more than their doctors? Experiences of health interactions after diabetes patient education: a qualitative patient-led study</a></td>\n</tr>\n<tr>\n<td width=\"50\">4</td>\n<td width=\"132\">Carpenter <em>et al</em></td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/content/3/5/e002299.full\" target=\"_blank\">Bed sharing when parents do not smoke: is there a risk of SIDS? An individual level analysis of five major case–control studies</a></td>\n</tr>\n<tr>\n<td width=\"50\">5</td>\n<td width=\"132\">Head <em>et al</em></td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/content/3/12/e003362.full\" target=\"_blank\">Differences in research funding for women scientists: a systematic comparison of UK investments in global infectious disease research during 1997–2010</a></td>\n</tr>\n<tr>\n<td width=\"50\">6</td>\n<td width=\"132\">Werb <em>et al</em></td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/content/3/9/e003077.full\" target=\"_blank\">The temporal relationship between drug supply indicators: an audit of international government surveillance systems</a></td>\n</tr>\n<tr>\n<td width=\"50\">7</td>\n<td width=\"132\">Nielsen <em>et al</em></td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/content/3/8/e003045.full\" target=\"_blank\">Mental health status and risk of new cardiovascular events or death in patients with myocardial infarction: a population-based cohort study</a></td>\n</tr>\n<tr>\n<td width=\"50\">8</td>\n<td width=\"132\">Doyle <em>et al</em></td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/content/3/1/e001570.full\" target=\"_blank\">A systematic review of evidence on the links between patient experience and clinical safety and effectiveness</a></td>\n</tr>\n<tr>\n<td width=\"50\">9</td>\n<td width=\"132\">Ford <em>et al</em></td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/content/3/3/e002269.full\" target=\"_blank\">Current treatments in diabetic macular oedema: systematic review and meta-analysis</a></td>\n</tr>\n<tr>\n<td width=\"50\">10</td>\n<td width=\"132\">Gehring <em>et al</em></td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/content/3/11/e002957.full\" target=\"_blank\">Factors influencing clinical trial site selection in Europe: the Survey of Attitudes towards Trial sites in Europe (the SAT-EU Study)</a></td>\n</tr>\n</tbody>\n</table>\n<p>&nbsp;</p>\n<p>Most read figures are based on pdf downloads and full text views. Abstract views are excluded.<!--TrendMD v2.4.3--></p>\n"},"site":{"siteMetadata":{"title":"Gatsby Default Starter"}}},"pathContext":{"id":"a2f081bf-ba36-5fea-84a5-51a9dfb0594a"}}

